import React, { Component } from 'react';
import { MAP } from 'react-google-maps/lib/constants'
import store from 'store'
import './App.css';

import Map from './components/Map'
import Panel from './components/Panel'

import { lovenestReviewsIndex } from './utils/algolia'

const geoNavigator = window.navigator.geolocation

const distance = (lat1, lon1, lat2, lon2, unit) => {
  const radlat1 = Math.PI * lat1/180
  const radlat2 = Math.PI * lat2/180
  const theta = lon1-lon2
  const radtheta = Math.PI * theta/180
  let dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
  dist = Math.acos(dist)
  dist = dist * 180/Math.PI
  dist = dist * 60 * 1.1515
  if (unit === 'K') { dist = dist * 1.609344 }
  if (unit === 'N') { dist = dist * 0.8684 }
  return dist
}

class App extends Component {
  constructor() {
    super()

    const currentCenter = store.get('currentCenter')
    const foundLocation = !!currentCenter
    const center = currentCenter || {
      lat: 40.7101006,
      lng: -74.0006684,
    }
    this.state = {
      center,
      reviews: null,
      selectedReview: null,
      height: '100vh',
      foundLocation,
    }
    this.panel = undefined
    this.mapMoved = false
  }

  componentDidMount() {
    this.fetchReviews()
    if (geoNavigator) {
      geoNavigator.getCurrentPosition((pos) => {

        const center = store.get('currentCenter') || this.state.center
        const { lat, lng } = center
        const { latitude, longitude } = pos.coords
        if (lat === latitude && lng === longitude) return

        const newCenter = {
          lat: latitude,
          lng: longitude,
        }
        store.set('currentCenter', newCenter)
        this.setState({
          foundLocation: true,
        })

        if (this.mapMoved && distance(lat, lng, latitude, longitude, 'K') < 16) return

        this.fetchReviews(newCenter)
        this.setState({
          center: newCenter,
        }, () => {
          const { center } = this.state
          const innerMap = this.map.context[MAP]
          if (innerMap && !this.mapMoved) {
            innerMap.setCenter(center)
          }
        })
      })
    }
    window.addEventListener('resize', this.onResize)
    this.onResize()
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onResize)
  }

  onCenterChanged = (center) => {
    this.setState({ center })
    this.mapMoved = true
  }

  onSelectReview = (review, fromMarker=false) => {
    if (fromMarker) {
      this.panel.scrollToReview(review)
    }

    this.setState({
      selectedReview: review,
    })
  }

  onResize = () => {
    // this is done cause iOS safari messes up vh units
    this.setState({
      height: `${Math.max(document.documentElement.clientHeight, window.innerHeight || 0)}px`,
    })
  }

  setMap = (c) => {
    this.map = c
  }

  setPanel = (c) => {
    this.panel = c
  }

  updateCenter = (center) => {
    this.map.updateCenter(center)
  }

  getCenter = () => {
    return this.state.center
  }

  fetchGoogleMap = () => {
    return this.map.fetchGoogleMap()
  }

  fetchReviews = (center=this.getCenter()) => {
    this.setState({
      results: null,
    })
    return lovenestReviewsIndex.search({
      query: '',
      aroundLatLng: `${center.lat},${center.lng}`,
      aroundRadius: 32000,
    }).then((res) => {
      this.setState({
        reviews: res.hits,
      })
    })
  }

  render() {
    const { center, reviews, selectedReview, height, foundLocation } = this.state

    return (
      <div className="app" style={{ height }}>
        <Map
          ref={this.setMap}
          center={center}
          reviews={reviews}
          onCenterChanged={this.onCenterChanged}
          onSelectReview={this.onSelectReview}
          selectedReview={selectedReview}
        />
        <Panel
          ref={this.setPanel}
          getCenter={this.getCenter}
          fetchGoogleMap={this.fetchGoogleMap}
          fetchReviews={this.fetchReviews}
          reviews={reviews}
          onSelectReview={this.onSelectReview}
          selectedReview={selectedReview}
          updateCenter={this.updateCenter}
          foundLocation={foundLocation}
        />
      </div>
    );
  }
}

export default App;



